export class ManagerBooleanBean {//此功能用于客户360视图企业客户经营信息查询，修改
    custNo: boolean=false;//客户号
    manageType: boolean=false;//经营性质
    mainBusScope: boolean=false;//主营范围
    otherBusScope: boolean=false;//兼营范围
    partnerNum: boolean=false;//股东或合伙人数
    entemplNum: boolean=false;//雇员人数
    businessArea: boolean=false;//经营场地面积
    ownerShip: boolean=false;//经营场地所有权
    mainProDesc: boolean=false;//主要产品情况
    mainCust: boolean=false;//主要客户
    suppliers: boolean=false;//主要供应商
    partnerBg: boolean=false;//股东背景
    highMgrStable: boolean=false;//企业高管及核心技术人员稳定性
    equipStatus: boolean=false;//技术设备状况
    indPolicy: boolean=false;//国家产业政策
    attributeSize: boolean=false;//规模属性
    custAnnualOutput: boolean=false;//客户年产值（元）
    managementLevel: boolean=false;//经营管理水平
    foreignInvestEnterpriseNumber: boolean=false;//对外投资企业个数
    smeType: boolean=false;//中小企业类型
    lastyearEmployeesNumber: boolean=false;//上年雇员人数（个）
    lastyearCustYearOutputvalue: boolean=false;//上年客户年产值（销售收入:万元）
    custAssetsTotalamount: boolean=false;//客户资产总额（万元）
    crossTypeDate: boolean=false;//划型日期
    businessProject:boolean=false;//经营项目
}

