import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Observable } from 'rxjs/Rx';
import { CustAddressInfoBean } from '../../../bean/custom-view.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { checkNumber, checkTel, postCodeCheck } from 'app/@uisftech/common/service/validator';
@Component({
  selector: 'app-update-address',
  templateUrl: './update-address.component.html',
  styleUrls: ['./update-address.component.scss'],
  providers: [Commfunc]
})
export class UpdateAddressComponent implements OnInit, OnChanges {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: any;
  buttonDisabled:boolean = false;
  msgs: Message[] = [];
  btnText: string;
  adopt: string;
  noadopt: string;
  arr: any;
  cars: any;
  // province:any;
  // city:any;
  // zone:any;
  addressTypeOptions: any[];//地址类型
  commonAddressOptions: any[];//是否常用地址
  // provinceOptins:any[];//省
  // cityOptions:any[];//市
  // zoneOptions:any[];//区
  custAddressInfoBean: CustAddressInfoBean = new CustAddressInfoBean();
  distributeCustQryForm: FormGroup;//校验  
  checkNumber = checkNumber;//校验数字
  checkTel = checkTel;//校验手机号
  postCodeCheck = postCodeCheck;//校验邮编
  provinceOptionsBasic: any[];//基础信息省
  cityOptionsBasic: any[];//基础信息市
  zoneOptionsBasic: any[];//基础信息区
  streetOptionsBasic: any[];//基础信息街道
  province: any;//省
  city: any;//市
  zone: any;//区
  street: any;//街道
  zipCodeMsg: boolean = false;//邮编长度错误信息控制
  zipCodeMsg1: boolean = false;//邮编类型错误信息控制
  // contactNumberMsg: boolean = false;//联系电话长度信息控制
  // contactNumberMsg1: boolean = false;//联系电话类型信息控制
  code: any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeValues();
  }

  ngOnInit() {
    this.commonAddressOptions = this.code['WHETHER_COMMONLY_USED_ADDRESS'];
    this.distributeCustQryForm = this.fb.group({
      'addressType': new FormControl('', Validators.required),
      // 'custName': new FormControl('',Validators.required),
      'province': new FormControl('', Validators.required),
      // 'relationshipCustName': new FormControl('', Validators.required),
      'city': new FormControl('', Validators.required),
      'zone': new FormControl(''),
      'street': new FormControl(''),
      'address2': new FormControl('', Validators.required),
      'zipCode': new FormControl('', Validators.required),
      'contactNumber': new FormControl('', Validators.required),
      'commonAddress': new FormControl(''),
    });
  }
  ngOnChanges() {
    if (this.kindDo == '1') {
      this.addressTypeOptions = this.code['PERSON_ADRESS_TYPE_A'];
    } else if (this.kindDo == '2') {
      this.addressTypeOptions = this.code['COMPANY_ADRESS_TYPE'];
    } else if (this.kindDo == '3') {
      this.addressTypeOptions = this.code['PERSON_ADRESS_TYPE_B'];
    }
    if (this.inValue.address1 && this.inValue.address1 != null && this.inValue.address1 != '') {
      this.httpService.queryParentZoneAll({ zoneId: this.inValue.address1 }).subscribe(data => {
        this.provinceOptionsBasic = this.commfunc.listToLabelvalue(data.firstZone, 'zoneName', 'zoneId');
        this.provinceOptionsBasic.unshift({ label: '请选择', value: '' });
        this.cityOptionsBasic = this.commfunc.listToLabelvalue(data.secondZone, 'zoneName', 'zoneId');
        this.cityOptionsBasic.unshift({ label: '请选择', value: '' });
        this.zoneOptionsBasic = this.commfunc.listToLabelvalue(data.thirdZone, 'zoneName', 'zoneId');
        this.zoneOptionsBasic.unshift({ label: '请选择', value: '' });
        this.streetOptionsBasic = this.commfunc.listToLabelvalue(data.fourthZone, 'zoneName', 'zoneId');
        this.streetOptionsBasic.unshift({ label: '请选择', value: '' });
        data.zoneList.forEach(item => {
          switch (item.parentZoneLevel) {
            case '1': this.province = item.parentZoneId; break;
            case '2': this.city = item.parentZoneId; break;
            case '3': this.zone = item.parentZoneId; break;
            case '4': this.street = item.parentZoneId; break;
            default: break;
          }
        })
      })
    } else {
      let a = '';
      this.queryZoneInfo(a);
    }
    let aa = _.clone(this.inValue);
    delete aa.lastUpdatedStamp;
    delete aa.zoneName;
    this.custAddressInfoBean = aa;

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  save() {
    //校验的
    for (const i in this.distributeCustQryForm.controls) {
      this.zipCodeMsg1 = false;
      this.distributeCustQryForm.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.distributeCustQryForm.controls) {
      this.zipCodeMsg1 = false;
      // this.contactNumberMsg1=false;
      if (!this.distributeCustQryForm.controls[i].valid) {
        return;
      }
    }
    if (!this.street || this.street == '') {
      this.custAddressInfoBean.address1 = this.zone;
      if (!this.zone || this.zone == '') {
        this.custAddressInfoBean.address1 = this.city;
        if (!this.city || this.city == '') {
          this.custAddressInfoBean.address1 = this.province;
          if (!this.province) {
            this.province == '';
            this.custAddressInfoBean.address1 == this.province;
          } else {
            this.custAddressInfoBean.address1 == this.province;
          }
        } else {
          this.custAddressInfoBean.address1 = this.city;
        }
      } else {
        this.custAddressInfoBean.address1 = this.zone;
      }
    } else {
      this.custAddressInfoBean.address1 = this.street;
    }
    let param = this.custAddressInfoBean;
    if (postCodeCheck(null, param.zipCode)) {
      this.zipCodeMsg1 = true;
      // this.msgs = [{ severity: 'error', summary: '提示', detail: '邮政编码输入有误,请输入6位数字正确的邮政编码' }];
      // this.custAddressInfoBean.zipCode='';
      return;
    }
    if (checkTel(null, param.contactNumber)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '联系电话输入有误,请输入11位数字正确的联系电话' }];
      this.custAddressInfoBean.contactNumber = '';
      return;
    }
    this.buttonDisabled = true;
    
    let observable = this.httpService.updateCustAddressInfo(param);
    observable.subscribe(data => {
      this.buttonDisabled = false;
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
        this.outValue.emit('修改地址');
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    });
  }
  close() {
    this.outValue.emit('取消');
  }
  onKeyupNumber(event, type, num) {
    this.zipCodeMsg = false;
    this.zipCodeMsg1 = false;
    // this.contactNumberMsg = false;
    // this.contactNumberMsg1 = false;
    if (event.target.value.length >= num) {
      event.target.value = '';
      if (type == 'zipCode') {
        this.zipCodeMsg = true;
      } else if (type == 'contactNumber') {
        // this.contactNumberMsg = true;
      }
    }
    if (checkNumber(null, event.target.value)) {
      if (type == 'zipCode') {
        this.zipCodeMsg1 = true;
        event.target.value = '';
      } else if (type == 'contactNumber') {
        // this.contactNumberMsg1 = true;
        event.target.value = '';
      }
    }
  }
  onBlurMobile(event) {
    if (checkTel(null, event.target.value)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '手机号码输入有误' }];
      event.target.value = '';
    }
  }

  onBlurZipCode(event) {
    if (postCodeCheck(null, event.target.value)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '邮政编码输入有误' }];
      event.target.value = '';
    }
  }
  queryZoneInfo(data) {
    if (data == '') {
      this.provinceOptionsBasic = [{ label: '请选择', value: '' }];
      this.cityOptionsBasic = [{ label: '请选择', value: '' }];
      this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
      this.streetOptionsBasic = [{ label: '请选择', value: '' }];
      this.province = '';
      this.city = '';
      this.zone = '';
      this.street = '';
      let param_zone = {
        parentZoneId: ''
      }
      //四级区划查询省
      this.httpService.queryZoneInfo(param_zone).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.provinceOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
          this.provinceOptionsBasic.unshift({ label: '请选择', value: '' });
          this.cityOptionsBasic = [{ label: '请选择', value: '' }];
          this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
          this.streetOptionsBasic = [{ label: '请选择', value: '' }];
          this.city = '';
          this.zone = '';
          this.street = '';
        }
      });
    } else if (data == '1') {
      if (this.province == '') {
        this.cityOptionsBasic = [{ label: '请选择', value: '' }];
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.city = '';
        this.zone = '';
        this.street = '';
      } else {
        this.cityOptionsBasic = [{ label: '请选择', value: '' }];
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.city = '';
        this.zone = '';
        this.street = '';
        let param_zone = {
          parentZoneId: this.province
        }
        //四级区划查询市
        this.httpService.queryZoneInfo(param_zone).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.cityOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
            this.cityOptionsBasic.unshift({ label: '请选择', value: '' });
            this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
            this.streetOptionsBasic = [{ label: '请选择', value: '' }];
            this.zone = '';
            this.street = '';
          }
        });
      }
    } else if (data == '2') {
      if (this.city == '') {
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.zone = '';
        this.street = '';
      } else {
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.zone = '';
        this.street = '';
        let param_zone = {
          parentZoneId: this.city
        }
        //四级区划查询区
        this.httpService.queryZoneInfo(param_zone).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.zoneOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
            this.zoneOptionsBasic.unshift({ label: '请选择', value: '' });
            this.streetOptionsBasic = [{ label: '请选择', value: '' }];
            this.street = '';
          }
        });
      }
    } else if (data == '3') {
      if (this.zone == '') {
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.street = '';
      } else {
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.street = '';
        let param_zone = {
          parentZoneId: this.zone
        }
        //四级区划查询街道
        this.httpService.queryZoneInfo(param_zone).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.streetOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
            this.streetOptionsBasic.unshift({ label: '请选择', value: '' });
          }
        });
      }
    }

  }

  onKeyupNumberMobile(event) {
    if (checkNumber(null, event.target.value)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '只能输入数字!' }];
      event.target.value = '';
      this.custAddressInfoBean.contactNumber = '';
    }
  }
}
