import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Observable } from 'rxjs/Rx';
import { CustCertificateInfoBean } from '../../../bean/custom-view.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
import * as moment from 'moment';
import { sixthTwoNum } from 'app/@uisftech/common/service/validator';
@Component({
  selector: 'app-update-certificate',
  templateUrl: './update-certificate.component.html',
  styleUrls: ['./update-certificate.component.scss'],
  providers: [Commfunc]
})
export class UpdateCertificateComponent implements OnInit, OnChanges {


  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  buttonDisabled:boolean = false;
  msgs: any = [];
  btnText: string;
  adopt: string;
  noadopt: string;
  arr: any;
  cars: any;
  idenTypeOptions: any[];//个人证件类型
  comIdenTypeOptions: any[];//企业证件类型
  custCertificateInfoBean: CustCertificateInfoBean = new CustCertificateInfoBean();
  distributeCustQryForm: FormGroup;//校验  
  countryOptions: any[] ;//发证国家
  registeredFundsCurrencyOptions: any[] ;//注册资金币种
  registrationTypeOptions: any[] ;//登记注册类型
  ch: any = CALENDAR_CN;//日期
  isDueTime1: boolean = false;
  isDueTime2: boolean = false;
  provinceOptionsBasic: any[];//基础信息省
  cityOptionsBasic: any[];//基础信息市
  zoneOptionsBasic: any[];//基础信息区
  streetOptionsBasic: any[];//基础信息街道
  province: any;//省
  city: any;//市
  zone: any;//区
  street: any;//街道
  showregisteredCapital: boolean = false;//控制资金校验的显示隐藏
  yearRangeSmall: any = yearRangeSmall;
  yearRangeBig: any = yearRangeBig;
  yearRange: any;
  code:any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeValues()
    this.countryOptions=this.code['COUNTRY'];
    this.registeredFundsCurrencyOptions=this.code['RegisteredFundsCurrency'];
    this.registrationTypeOptions=this.code['RegistrationType']
  }

  ngOnInit() {
    this.idenTypeOptions =this.code['IDEN_TYPE'] ;
    this.comIdenTypeOptions =this.code['CO_IDEN_TYPE'] ;
    // this.distributeCustQryForm = this.fb.group({
    //   'idenType': new FormControl('', Validators.required),
    //   'idenNum': new FormControl('', Validators.required),
    //   'issueDate': new FormControl('', Validators.required),
    //   'certificateDate': new FormControl('', Validators.required),
    //   'issuingAuthority': new FormControl(''),
    //   'registeredAddress': new FormControl('', Validators.required),
    // });
  }
//码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  ngOnChanges() {
    this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
    if (this.keyLabel) {
      this.keyLabel = this.keyLabel;
      if (this.keyLabel == '1') {
        this.distributeCustQryForm = this.fb.group({
          'idenType': new FormControl('', Validators.required),
          'idenNum': new FormControl('', [Validators.pattern(/^[A-Za-z0-9]+$/), Validators.required]),
          'issueDate': new FormControl('', Validators.required),
          'certificateDate': new FormControl('', Validators.required),
          'issuingAuthority': new FormControl(''),
          // 'registeredAddress': new FormControl('', Validators.required),
          'province': new FormControl('', Validators.required),
          'city': new FormControl('', Validators.required),
          'zone': new FormControl(''),
          'street': new FormControl(''),
          'comments': new FormControl(''),
        });
      } else if (this.keyLabel == '2') {
        this.distributeCustQryForm = this.fb.group({
          'idenType': new FormControl('', Validators.required),
          'idenNum': new FormControl('', Validators.required),
          // 'idenNum': new FormControl('', [Validators.pattern(/^[A-Za-z0-9]+$/), Validators.required]),
          'registrationType': new FormControl('', Validators.required),
          'registeredFundsCurrency': new FormControl('', Validators.required),
          'registeredCapital': new FormControl('', Validators.required),
          'issueDate': new FormControl('', Validators.required),
          'certificateDate': new FormControl('', Validators.required),
          'issuingAuthority': new FormControl(''),
          'issuingLicenseCountry': new FormControl(''),
          // 'registeredAddress': new FormControl('', Validators.required),
          'province': new FormControl('', Validators.required),
          'city': new FormControl('', Validators.required),
          'zone': new FormControl(''),
          'street': new FormControl(''),
          'comments': new FormControl(''),
        });
      }
    }
    if (this.inValue) {
      if (this.inValue.registeredAddress && this.inValue.registeredAddress != null && this.inValue.registeredAddress != '') {
        this.httpService.queryParentZoneAll({ zoneId: this.inValue.registeredAddress }).subscribe(data => {
          this.provinceOptionsBasic = this.commfunc.listToLabelvalue(data.firstZone, 'zoneName', 'zoneId');
          this.provinceOptionsBasic.unshift({ label: '请选择', value: '' });
          this.cityOptionsBasic = this.commfunc.listToLabelvalue(data.secondZone, 'zoneName', 'zoneId');
          this.cityOptionsBasic.unshift({ label: '请选择', value: '' });
          this.zoneOptionsBasic = this.commfunc.listToLabelvalue(data.thirdZone, 'zoneName', 'zoneId');
          this.zoneOptionsBasic.unshift({ label: '请选择', value: '' });
          this.streetOptionsBasic = this.commfunc.listToLabelvalue(data.fourthZone, 'zoneName', 'zoneId');
          this.streetOptionsBasic.unshift({ label: '请选择', value: '' });
          data.zoneList.forEach(item => {
            switch (item.parentZoneLevel) {
              case '1': this.province = item.parentZoneId; break;
              case '2': this.city = item.parentZoneId; break;
              case '3': this.zone = item.parentZoneId; break;
              case '4': this.street = item.parentZoneId; break;
              default: break;
            }
          })
        })
      } else {
        let a = '';
        this.queryZoneInfo(a);
      }
      let aa = this.inValue;
      aa.issueDate = new Date(aa.issueDate);
      aa.certificateDate = new Date(aa.certificateDate);
      this.custCertificateInfoBean = aa;

    }
  }

  save() {
    //校验的
    for (const i in this.distributeCustQryForm.controls) {
      this.distributeCustQryForm.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.distributeCustQryForm.controls) {
      if (!this.distributeCustQryForm.controls[i].valid) {
        return;
      }
    }
    this.chooseDate();
    this.custCertificateInfoBean.custNo = this.inValue.custNo;
    // if(this.keyLabel=='2'){
    if (!this.street || this.street == '') {
      this.custCertificateInfoBean.registeredAddress = this.zone;
      if (!this.zone || this.zone == '') {
        this.custCertificateInfoBean.registeredAddress = this.city;
        if (!this.city || this.city == '') {
          this.custCertificateInfoBean.registeredAddress = this.province;
          if (!this.province) {
            this.province == '';
            this.custCertificateInfoBean.registeredAddress == this.province;
          } else {
            this.custCertificateInfoBean.registeredAddress == this.province;
          }
        } else {
          this.custCertificateInfoBean.registeredAddress = this.city;
        }
      } else {
        this.custCertificateInfoBean.registeredAddress = this.zone;
      }
    } else {
      this.custCertificateInfoBean.registeredAddress = this.street;
    }
    // }
    let param = _.clone(this.custCertificateInfoBean);
    param.issueDate = this.commfunc.transDate(param.issueDate);
    param.certificateDate = this.commfunc.transDate(param.certificateDate);
    //组织机构代码校验
    if (this.custCertificateInfoBean.idenType == '12') {
      var reg = /^[A-Za-z0-9]{8}[-]{0,1}[A-Za-z0-9]{1}$/;
      if (!reg.test(this.custCertificateInfoBean.idenNum)) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "组织机构代码长度为9位或10位，长度为10位时第9位为'-'" });
        return;
      }
    }
    if (param.issueDate > param.certificateDate) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '证件到期日期不能早于证件签发日期' }];
      return;
    }
    this.buttonDisabled = true;
    
    let observable = this.httpService.updateCustCertificateInfo(param);
    //添加证件信息 
    observable.subscribe(data => {
      this.buttonDisabled = false;
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: "修改成功!" }];
        this.outValue.emit('修改证件');
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }

  // 校验证件截止日期在未来三个月以内
  isDue() {
    this.isDueTime2 = false;
    this.isDueTime1 = false;
    let date = new Date();
    let dueMonth = +moment().month(3);
    if (this.custCertificateInfoBean.certificateDate) {
      let chooseTime: any = this.custCertificateInfoBean.certificateDate
      let chooseTimeMonth = chooseTime.getMonth() + 1;
      if (date.getTime() > chooseTime.getTime()) {
        this.isDueTime2 = true;
      } else {
        if (chooseTimeMonth < dueMonth) {
          this.isDueTime1 = true;
        }
      }
    }
  }
  chooseDate() {
    this.isDueTime2 = false;
    this.isDueTime1 = false;
    let date = new Date();
    let dueMonth = moment().add(3, 'month').format('Y-MM-DD');
    if (this.commfunc.transDateN(this.custCertificateInfoBean.certificateDate) <= dueMonth && this.commfunc.transDateN(this.custCertificateInfoBean.certificateDate) >= this.commfunc.transDateN(date)) {
      this.isDueTime1 = true;
      this.isDueTime2 = false;
    } else if (this.commfunc.transDateN(this.custCertificateInfoBean.certificateDate) <= this.commfunc.transDateN(date)) {
      this.isDueTime2 = true;
      this.isDueTime1 = false;
    }
  }

  close() {
    this.outValue.emit('取消');
  }
  queryZoneInfo(data) {
    if (data == '') {
      this.provinceOptionsBasic = [{ label: '请选择', value: '' }];
      this.cityOptionsBasic = [{ label: '请选择', value: '' }];
      this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
      this.streetOptionsBasic = [{ label: '请选择', value: '' }];
      this.province = '';
      this.city = '';
      this.zone = '';
      this.street = '';
      let param_zone = {
        parentZoneId: ''
      }
      //四级区划查询省
      this.httpService.queryZoneInfo(param_zone).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.provinceOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
          this.provinceOptionsBasic.unshift({ label: '请选择', value: '' });
          this.cityOptionsBasic = [{ label: '请选择', value: '' }];
          this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
          this.streetOptionsBasic = [{ label: '请选择', value: '' }];
          this.city = '';
          this.zone = '';
          this.street = '';
        }
      });
    } else if (data == '1') {
      if (this.province == '') {
        this.cityOptionsBasic = [{ label: '请选择', value: '' }];
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.city = '';
        this.zone = '';
        this.street = '';
      } else {
        this.cityOptionsBasic = [{ label: '请选择', value: '' }];
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.city = '';
        this.zone = '';
        this.street = '';
        let param_zone = {
          parentZoneId: this.province
        }
        //四级区划查询市
        this.httpService.queryZoneInfo(param_zone).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.cityOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
            this.cityOptionsBasic.unshift({ label: '请选择', value: '' });
            this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
            this.streetOptionsBasic = [{ label: '请选择', value: '' }];
            this.zone = '';
            this.street = '';
          }
        });
      }
    } else if (data == '2') {
      if (this.city == '') {
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.zone = '';
        this.street = '';
      } else {
        this.zoneOptionsBasic = [{ label: '请选择', value: '' }];
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.zone = '';
        this.street = '';
        let param_zone = {
          parentZoneId: this.city
        }
        //四级区划查询区
        this.httpService.queryZoneInfo(param_zone).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.zoneOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
            this.zoneOptionsBasic.unshift({ label: '请选择', value: '' });
            this.streetOptionsBasic = [{ label: '请选择', value: '' }];
            this.street = '';
          }
        });
      }
    } else if (data == '3') {
      if (this.zone == '') {
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.street = '';
      } else {
        this.streetOptionsBasic = [{ label: '请选择', value: '' }];
        this.street = '';
        let param_zone = {
          parentZoneId: this.zone
        }
        //四级区划查询街道
        this.httpService.queryZoneInfo(param_zone).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.streetOptionsBasic = this.commfunc.listToLabelvalue(data.zoneList, 'zoneName', 'zoneId');
            this.streetOptionsBasic.unshift({ label: '请选择', value: '' });
          }
        });
      }
    }
  }
  //16位整数或16位整数保留两位小数
  onkeyupSixtwoNum(event) {
    if (sixthTwoNum(null, event.target.value)) {
      event.target.value = '';
      this.showregisteredCapital = true;
    } else {
      this.showregisteredCapital = false;
    }
  }
}
