import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
// import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { BeneficiaryBean } from '../bean/checkBoolean.bean';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';

@Component({
    selector: 'app-beneficiary',
    templateUrl: './beneficiary.component.html',
    styleUrls: ['./beneficiary.component.scss']
})
export class BeneficiaryComponent implements OnInit {

    @Output() outValue = new EventEmitter();
    @Input('inValue') inValue: any;

    code: any;//码值
    CERT_TYPE: any;//
    ch: any = CALENDAR_CN;
    userform: FormGroup; //校验
    beneficiaryBean: BeneficiaryBean = new BeneficiaryBean();
    msgs: Message[] = [];

    constructor(
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) {
        this.codeVal();

        this.CERT_TYPE = this.code['CERT_TYPE_PERSON'];  //个人客户证件类型
    }

    ngOnInit() {
        if (this.inValue && this.inValue != '') {//修改
            this.beneficiaryBean = this.inValue;
        }
        this.preset();
    }

    //初始化
    preset() {
        //表单校验
        this.userform = this.fb.group({
            'beneficiaryName': new FormControl('', [Validators.required]),
            'certificateType': new FormControl('', [Validators.required]),
            'certificateNumber': new FormControl('', [Validators.required]),
            'certificateDeadline': new FormControl('', [Validators.required])
        });
    }

    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    //提交
    submitBtn() {
        //校验
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return false;
            }
        }

        this.outValue.emit(this.beneficiaryBean);
    }

    //
    change(event){
        console.log(event);
        if(event.value == '0'){//身份证
            this.userform.removeControl('certificateNumber');
            this.userform.addControl('certificateNumber', this.fb.control('', [Validators.required,Validators.pattern(/^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/)]));
        }else{
            this.userform.removeControl('certificateNumber');
            this.userform.addControl('certificateNumber', this.fb.control('', [Validators.required]));
        }
    }
}
