import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
import { Observable } from 'rxjs/Rx';
import { CustWorkunitInfoBean } from '../../../bean/custom-view.bean';
import * as _ from 'lodash';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-update-work-company',
  templateUrl: './update-work-company.component.html',
  styleUrls: ['./update-work-company.component.scss'],
  providers: [Commfunc]
})
export class UpdateWorkCompanyComponent implements OnInit, OnChanges {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  buttonDisabled:boolean = false;
  msgs: any = [];
  btnText: string;
  adopt: string;
  noadopt: string;
  conmpanyNatureOptions: any[];//单位性质
  occupationOptions: any[];//职业
  dutyOptions: any[];//职务
  jobTitleOptions: any[];//职称
  workNatureOptions: any[];//工作性质
  custWorkunitInfoBean: CustWorkunitInfoBean = new CustWorkunitInfoBean();
  distributeCustQryForm: FormGroup;//校验  
  ch: any = CALENDAR_CN;//日期
  firstConmpanyNature: any;
  secondConmpanyNature: any;
  firstConmpanyNatureOptions: any[];
  secondConmpanyNatureOptions: any[];
  isDueTime: boolean = false; //离职时间是否大于入职时间
  yearRangeSmall: any = yearRangeSmall;
  yearRangeBig: any = yearRangeBig;
  yearRange: any;
  //职业
  LevelAOptionsBasic: any[];
  LevelBOptionsBasic: any[];
  LevelCOptionsBasic: any[];
  occupationLevelA: any;
  occupationLevelB: any;
  occupationLevelC: any;
  code:any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeValues();

  }

  ngOnInit() {
    this.conmpanyNatureOptions =this.code['ConmpanyNature'] ;
    this.occupationOptions =this.code['Occupation'] ;
    this.dutyOptions =this.code['Duty'] ;
    this.jobTitleOptions =this.code['JobTitle'] ;
    this.workNatureOptions =this.code['WorkNature'] ;
    this.distributeCustQryForm = this.fb.group({
      'firstConmpanyNature': new FormControl('', Validators.required),
      'secondConmpanyNature': new FormControl('', Validators.required),
      'workUnit': new FormControl('', Validators.required),
      'workNatureType': new FormControl(''),
      // 'occupation': new FormControl('', Validators.required),
      'duty': new FormControl('', Validators.required),
      'jobTitle': new FormControl('', Validators.required),
      'entryTime': new FormControl('', Validators.required),
      'departureTime': new FormControl(''),
      'occupationLevelA': new FormControl('', Validators.required),
      'occupationLevelB': new FormControl('', Validators.required),
      'occupationLevelC': new FormControl('', Validators.required),
    });
  }
  ngOnChanges() {
    this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
    if (this.inValue) {
      let aa = _.clone(this.inValue);
      if (this.inValue.entryTime && this.inValue.entryTime != '') {
        aa.entryTime = new Date(this.inValue.entryTime);
      }
      if (this.inValue.departureTime && this.inValue.departureTime != '') {
        aa.departureTime = new Date(this.inValue.departureTime);
      }
      this.custWorkunitInfoBean = aa;
      if (this.custWorkunitInfoBean.conmpanyCode && this.custWorkunitInfoBean.conmpanyCode != '') {
        this.httpService.queryTaskByIndtstryAll({ taskTypeId: this.custWorkunitInfoBean.conmpanyCode, taskTypeKey: 'conmpanyNature' }).subscribe(data => {
          this.firstConmpanyNatureOptions = this.commfunc.listToLabelvalue(data.firstTask, 'taskTypeName', 'taskTypeId');
          this.firstConmpanyNatureOptions.unshift({ label: '请选择', value: '' });
          this.secondConmpanyNatureOptions = this.commfunc.listToLabelvalue(data.secondTask, 'taskTypeName', 'taskTypeId');
          this.secondConmpanyNatureOptions.unshift({ label: '请选择', value: '' });
          data.taskList.forEach(item => {
            switch (item.level) {
              case '1': this.firstConmpanyNature = item.taskTypeId; break;
              case '2': this.secondConmpanyNature = item.taskTypeId; break;
              default: break;
            }
          })
        });
      } else {
        let a = '';
        this.queryConmpanyNature(a);
      }
      if (this.custWorkunitInfoBean.occupation && this.custWorkunitInfoBean.occupation != '') {
        this.httpService.queryTaskByIndtstryAll({ taskTypeId: this.custWorkunitInfoBean.occupation, taskTypeKey: 'occupation' }).subscribe(data => {
          this.LevelAOptionsBasic = this.commfunc.listToLabelvalue(data.firstTask, 'taskTypeName', 'taskTypeId');
          this.LevelAOptionsBasic.unshift({ label: '请选择', value: '' });
          this.LevelBOptionsBasic = this.commfunc.listToLabelvalue(data.secondTask, 'taskTypeName', 'taskTypeId');
          this.LevelBOptionsBasic.unshift({ label: '请选择', value: '' });
          this.LevelCOptionsBasic = this.commfunc.listToLabelvalue(data.thirdTask, 'taskTypeName', 'taskTypeId');
          this.LevelCOptionsBasic.unshift({ label: '请选择', value: '' });
          data.taskList.forEach(item => {
            switch (item.level) {
              case '1': this.occupationLevelA = item.taskTypeId; break;
              case '2': this.occupationLevelB = item.taskTypeId; break;
              case '3': this.occupationLevelC = item.taskTypeId; break;
              default: break;
            }
          })
        });
      } else {
        let cnonpro = '';
        this.queryOccupationInfo(cnonpro);
      }
    }
  }
  //码值
codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code= ret //code是自己声明的变量
  }
  save() {
    //职业
    if (!this.occupationLevelC || this.occupationLevelC == '') {
      if (!this.occupationLevelB || this.occupationLevelB == '') {
        this.custWorkunitInfoBean.occupation = this.occupationLevelA;
      } else {
        this.custWorkunitInfoBean.occupation = this.occupationLevelB;
      }
    } else {
      this.custWorkunitInfoBean.occupation = this.occupationLevelC;
    }
    //校验的
    for (const i in this.distributeCustQryForm.controls) {
      this.distributeCustQryForm.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.distributeCustQryForm.controls) {
      if (!this.distributeCustQryForm.controls[i].valid) {
        return;
      }
    }
    this.custWorkunitInfoBean.conmpanyCode = this.secondConmpanyNature;

    if (this.isDue() == 1) {
      return;
    }

    let param = _.clone(this.custWorkunitInfoBean);
    param.entryTime = this.commfunc.transDate(param.entryTime);
    param.departureTime = this.commfunc.transDate(param.departureTime);
    this.buttonDisabled = true;
    
    this.httpService.updateCustWorkunitInfo(param).subscribe(data => {
      this.buttonDisabled = false;
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
        this.outValue.emit('修改工作单位');
      } else {
        data.returnCode.message ? data.returnCode.message : '调用服务失败';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    })
  }

  // 离职时间是否大于入职时间
  isDue() {
    this.isDueTime = false;
    if (this.custWorkunitInfoBean.departureTime) {
      let entryTime: any = this.custWorkunitInfoBean.entryTime
      let quitTime: any = this.custWorkunitInfoBean.departureTime
      if (entryTime.getTime() > quitTime.getTime()) {
        this.isDueTime = true;
        return 1;
      }
    }
  }

  close() {
    this.outValue.emit('取消');
  }
  queryConmpanyNature(data) {
    if (data == '') {
      this.firstConmpanyNatureOptions = [{ label: '请选择', value: '' }];
      this.secondConmpanyNatureOptions = [{ label: '请选择', value: '' }];
      this.firstConmpanyNature = '';
      this.secondConmpanyNature = '';
      let param = {
        taskTypeParentId: '',
        taskTypeKey: 'conmpanyNature'
      }
      this.httpService.taskTypeQry(param).subscribe(data => {
        this.firstConmpanyNatureOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
        this.firstConmpanyNatureOptions.unshift({ label: '请选择', value: '' });
        this.secondConmpanyNatureOptions = [{ label: '请选择', value: '' }];
        this.firstConmpanyNature = '';
        this.secondConmpanyNature = '';
      });
    } else if (data == 1) {
      if (this.firstConmpanyNature == '') {
        this.secondConmpanyNatureOptions = [{ label: '请选择', value: '' }];
        this.secondConmpanyNature = '';
      } else {
        this.secondConmpanyNatureOptions = [{ label: '请选择', value: '' }];
        this.secondConmpanyNature = '';
        let param = {
          taskTypeParentId: this.firstConmpanyNature,
          taskTypeKey: 'conmpanyNature'
        }
        this.httpService.taskTypeQry(param).subscribe(data => {
          this.secondConmpanyNatureOptions = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
          this.secondConmpanyNatureOptions.unshift({ label: '请选择', value: '' });
          this.secondConmpanyNature = '';
        });
      }
    }
  }
  //选择职业
  queryOccupationInfo(data) {
    if (data == '') {
      let param_occupation = {
        taskTypeKey: 'occupation',
        taskTypeParentId: ''
      }
      //一级职业查询
      this.httpService.taskTypeQry(param_occupation).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.LevelAOptionsBasic = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
          this.LevelAOptionsBasic.unshift({ label: '请选择', value: '' });
          this.LevelBOptionsBasic = [{ label: '请选择', value: '' }];
          this.LevelCOptionsBasic = [{ label: '请选择', value: '' }];
          this.occupationLevelB = '';
          this.occupationLevelC = '';
        }
      });
    } else if (data == '1') {
      if (this.occupationLevelA == '') {
        this.LevelBOptionsBasic = [{ label: '请选择', value: '' }];
        this.LevelCOptionsBasic = [{ label: '请选择', value: '' }];
        this.occupationLevelB = '';
        this.occupationLevelC = '';
      } else {
        let param_occupation = {
          taskTypeKey: 'occupation',
          taskTypeParentId: this.occupationLevelA,
        }
        //二级职业查询
        this.httpService.taskTypeQry(param_occupation).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.LevelBOptionsBasic = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
            this.LevelBOptionsBasic.unshift({ label: '请选择', value: '' });
            this.LevelCOptionsBasic = [{ label: '请选择', value: '' }];
            this.occupationLevelC = '';
          }
        });
      }
    } else if (data == '2') {
      if (this.occupationLevelB == '') {
        this.LevelCOptionsBasic = [{ label: '请选择', value: '' }];
        this.occupationLevelC = '';
      } else {
        let param_occupation = {
          taskTypeKey: 'occupation',
          taskTypeParentId: this.occupationLevelB,
        }
        //三级职业查询
        this.httpService.taskTypeQry(param_occupation).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.LevelCOptionsBasic = this.commfunc.listToLabelvalue(data.taskTypeList, 'taskTypeName', 'taskTypeId');
            this.LevelCOptionsBasic.unshift({ label: '请选择', value: '' });
          }
        });
      }
    }
  }
}
