    //提交按钮
    submitBtn() {
        // if(!this.addPartyRelationshipBean.relationshipCustNo){
        //     this.showEmpty = false;
        //     return;
        // }
        //校验的
        for (const i in this.distributeCustQryForm.controls) {
            this.distributeCustQryForm.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.distributeCustQryForm.controls) {
            if (!this.distributeCustQryForm.controls[i].valid) {
                return;
            }
        }
        // this.addPartyRelationshipBean.weakIdenFlag = '3';//强弱关联标识，后台改造完毕后删除
        // this.custNo = this.addPartyRelationshipBean.custNo;
        let param = _.clone(this.addPartyRelationshipBean);
        param.investTime = this.commfunc.transDate(param.investTime);
        param.idenExpiraDate = this.commfunc.transDate(param.idenExpiraDate);
        let ob = this.httpService.addHolderBaseInfo(param);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.addPartyRelationshipBean = new HolderBean();
                this.outValue.emit('新增股东');
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务！' });
        })
    }
