import { Component, OnInit, OnChanges,Output,EventEmitter } from '@angular/core';
import { CusViewHttpService } from '../../http/custom-view/custom-view.http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import {GlobalState} from '../../../../../global.state';
@Component({
  selector: 'app-general-info',
  templateUrl: './general-info.component.html',
  styleUrls: ['./general-info.component.scss'],
  providers: [Commfunc]
})
/**
 * 基础信息
 */
export class GeneralInfoComponent implements OnInit {
  @Output() totoSimpleInIn=new EventEmitter();
  custNo: string;
  custAttribute: string;
  toLCFX:any;
  constructor(
    public httpService: CusViewHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc,
    public globalState: GlobalState,
  ) {
    this.custNo = this.commfunc.getSessionData('custNo');
    // this.custNo="1300002163";
    this.toLCFX=this.route.snapshot.params['lcpg'];
    console.log(this.toLCFX);
  }

  ngOnInit() {
    let param1 = {
      custNo: this.custNo
    }
    this.httpService.selectCustBriefInfo(param1).subscribe(data => {
      this.custAttribute = data.custAttribute;
      this.commfunc.setSessionData('custType', data.custType);
    })
    this.globalState.subscribe('event:custom-view-refresh',()=>{
      this.selectCustBriefInfo();
    })
  }

  selectCustBriefInfo(){
    let param1 = {
      custNo: this.custNo
    }
    this.httpService.selectCustBriefInfo(param1).subscribe(data => {
      this.custAttribute = data.custAttribute;
      this.commfunc.setSessionData('custType', data.custType);
    })
  }

  addCall(event){
    this.toLCFX=event;
  }

}
