  custCertificateInfoBean: CustCertificateInfoBean = new CustCertificateInfoBean();//个人客户证件信息
  custWorkunitInfoBean: CustWorkunitInfoBean = new CustWorkunitInfoBean();//个人客户工作单位信息
  custAddressInfobean: CustAddressInfoBean = new CustAddressInfoBean();//客户地址信息
  custFamilyInfoBean: CustFamilyInfoBean = new CustFamilyInfoBean();//客户家庭信息
  custContactPreferencesBean: CustContactPreferencesBean = new CustContactPreferencesBean();//客户偏好联系
  preferMethod: any[] = [];//偏好联系方式
  preferAddress: any[] = [];//偏好联系地址
  acceptMsg: any[] = [];//不接受营销短信
  acceptCall: any[] = [];//不接受营销电话
  acceptMarket: any[] = [];//不参加营销活动
  custType: any;//判断证件显示
  addressOptions: any[];
  familyrelationitems: any;
  detailValue: any;
  detailDisplay: boolean = false;
  provinceOptionsBasic: any[];//基础信息省
  cityOptionsBasic: any[];//基础信息市
  zoneOptionsBasic: any[];//基础信息区
  streetOptionsBasic: any[];//基础信息街道
  province: any;//省
  city: any;//市
  zone: any;//区
