  //点击删除地址
  deleteAddress(data) {
    this.confirmationService.confirm({
      message: '是否确认删除?',
      header: '删除',
      icon: 'fa fa-question-circle',
      accept: () => {
        let paramdel = {
          custNo: data.custNo,
          contactMechId: data.contactMechId
        }
        let paramsel = {
          custNo: data.custNo
        }
        let observabledel = this.httpService.deleteCustAddressInfo(paramdel);
        let observablesel = this.httpService.selectCustAddressInfo(paramsel);
        observabledel.subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [{ severity: 'success', summary: '提示', detail: data.returnCode.message }];
            observablesel.subscribe(data => {
              if (data.returnCode.code == 'success') {
                for (let item of data.addressList) {
                  this.httpService.queryParentZoneInfo({ zoneId: item.address1 }).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                      let param = '';
                      data.zoneList = data.zoneList.reverse();
                      for (let item1 of data.zoneList) {
                        param += item1.parentZoneName
                      }
                      item['zoneName'] = param;
                    }
                  })
                }
                this.addressitems = data.addressList;
              }
            })
          } else {
            data.returnCode.message ? data.returnCode.message : '调用服务失败';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
          }
        });
      }
      // ,
      // reject: () => {
      //   this.msgs = [{ severity: 'success', summary: '提示', detail: '取消删除' }];
      // }
    });
  }
