  keyupLength(event, data) {
    if (this.getLength(event.target.value) > data) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: `此输入框所输内容必须为${Math.floor(data / 3)}个汉字或${data}个英文！` }];
      event.target.value = '';
    }
  }

  getLength(str) {
    let realLength = 0, len = str.length, charCode = -1;
    for (let i = 0; i < str.length; i++) {
      charCode = str.charCodeAt(i);
      if (charCode >= 0 && charCode <= 128) {
        realLength += 1;
      } else {
        realLength += 3;
      }
    }
    return realLength;
  }
