    onKeyupNumber(event) {
        if (checkNumber(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字' }];
            event.target.value = '';
        }
    }
    onBlurMobile(event, data) {
        if (data == 1) {
            if (event.target.value != '') {
                if (checkTel(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '手机号码输入有误' }];
                    event.target.value = '';
                }
            }
        } else if (data == 2) {
            if (phoneCheck(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '联系电话输入有误' }];
                event.target.value = '';
            }
        }
    }
    //16位整数或16位整数保留两位小数
    onkeyupSixtwoNum(event) {
        if (sixthTwoNum(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '所输金额必须为0-16位正整数或0-16位正整数保留两位小数' }];
            event.target.value = '';
            this.addPartyRelationshipBean.investAmount = '';
        }
    }
    //16位整数或16位整数保留两位小数
    onkeyupSixtwoNum1(event, data) {
        if (data == 1) {
            if (sixthTwoNum(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '所输股份占比必须为保留两位小数' }];
                event.target.value = '';
                this.addPartyRelationshipBean.shareProportion = '';
            }
            if (event.target.value > 100 || event.target.value < 0) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '所输股份占比只能输入0-100的数' }];
                event.target.value = '';
                this.addPartyRelationshipBean.shareProportion = '';
            }
        } else {
            if (sixthTwoNum(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '所输金额必须为0-16位正整数或0-16位正整数保留两位小数' }];
                event.target.value = '';
                this.addPartyRelationshipBean.shareProportion = '';
            }
        }
    }
 //   工作时间校验
 onkeyupthreeTime(event) {
    if (threeTwoNum(null, event.target.value)) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: '只能输入0-999的整数' }];
        event.target.value = '';
        this.addPartyRelationshipBean.workWeeklyTime='';
    }
  }
    validDate(){
    
        this.showDate=0;
        let a=_.clone(this.addPartyRelationshipBean);
        let ret:any = this.commfunc.transDate(new Date())
        let ret1 = ret*1 +1;
        let retSelect:any = this.commfunc.transDate(a.idenExpiraDate);
        if(this.commfunc.transDate(a.idenExpiraDate)<this.commfunc.transDate(new Date())){
            this.showDate=2;
        }else if(this.commfunc.transDate(a.idenExpiraDate)==ret){
            this.showDate=1;
        }
        else if( retSelect == ret1){ 
            this.showDate=1;
        }else{
            this.showDate=0;
        }  
        console.log(this.showDate)  
    }
    doSeletH() {
        if (this.addPartyRelationshipBean.holderType == '2') {
            this.IdenType =this.code['CO_IDEN_TYPE'];
            this.addPartyRelationshipBean.idenType = '';
        } else {
            this.IdenType =this.code['IDEN_TYPE'];
            this.addPartyRelationshipBean.idenType = '';
        }
    }
