import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
// import { SelectPartyRelationshipBean } from '../bean/selectPartyRelationship.bean';
import { KeyPersonBean } from '../../../../custom-view/association-relation/personal-relation/bean/addPartyRelationship.bean';
// import { Panduan, ReductionNumberType, GENDER, KEY_PERSON, ACADEMIC, IDEN_TYPE, JobTitle, Duty } from '../../../../constant/custView.codeValue';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import *  as _ from 'lodash';
import { checkChinese, checkNumber, checkTel, phoneCheck } from 'app/@uisftech/common/service/validator';
import { CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-update-key-person',
  templateUrl: './update-key-person.component.html',
  styleUrls: ['./update-key-person.component.scss']
})
export class UpdateKeyPersonComponent implements OnInit, OnChanges {
  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  addPartyRelationshipBean: KeyPersonBean = new KeyPersonBean();
  panduan: any[] = [];
  reductionNumberType: any[] = [];
  relationshipTypeOptions: any[] = [];
  // parentTypeId: any[] = ParentTypeId;
  distributeCustQryForm: FormGroup;//校验
  checkNumber = checkNumber;//校验数字
  checkTel = checkTel;//校验手机号
  phoneCheck = phoneCheck;//校验联系电话
  custNo: string;
  items: any[];
  parentId: any = [];
  parentTypeId: string;//父级关系类型编号
  relationshipType: any = [];//子级关系类型编号
  relationship: any = [];
  principalLinkman: any;
  principalLinkmanOrg: any;
  whether: boolean = true;
  whetherCredit: boolean = true;
  ch = CALENDAR_CN;
  academic = [];
  IdenType = [];
  JobTitle = [];
  gender = [];
  Duty = [];
  today: Date = new Date();
  //提示信息
  msgs: Message[] = [];
  show: boolean = true;
  showEmpty: boolean = true;
  param: any;
  showDate: any;
  showIden: boolean = false;
  code: any;
  yearRangeSmall: any = yearRangeSmall;
  yearRangeBig: any = yearRangeBig;
  yearRange: any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
    this.codeVal();
    this.panduan = this.code['Panduan'];
    this.reductionNumberType = this.code['ReductionNumberType'];
    this.relationshipTypeOptions = this.code['KEY_PERSON'];
    this.academic = this.code['ACADEMIC'];
    this.IdenType = this.code['IDEN_TYPE'];
    this.JobTitle = this.code['JobTitle'];
    this.gender = this.code['GENDER'];
    this.Duty = this.code['Duty'];
    //---------------------------------------------上面是码值
    this.custNo = this.commfunc.getSessionData('custNo');
  }

  ngOnInit() {

    this.distributeCustQryForm = this.fb.group({
      'custNo': new FormControl(''),//客户编码
      'keyCustname': new FormControl('', Validators.required),//姓名
      'gender': new FormControl(''),//性别
      'birthDay': new FormControl(''),//出生日期
      'idenType': new FormControl(''),//证件类型
      'idenNum': new FormControl(''),//证件号码
      'keyType': new FormControl(''),//关键人类别
      'duty': new FormControl(''),//职务
      'idenExpiraDate': new FormControl(''),//证件截止日期      
      // 'idenExpiraDate': new FormControl('', Validators.required),//证件截止日期
      'jobTitle': new FormControl(''),//职称
      'academic': new FormControl(''),//最高学历
      'interest': new FormControl(''),//个人爱好
      'mobileNum1': new FormControl(''),//手机1
      'mobileNum2': new FormControl(''),//手机2
      'telNum': new FormControl(''),//固话
      'address': new FormControl(''),//地址
      'detailAddr': new FormControl(''),//详细地址
      'resume': new FormControl(''),//工作简历
    });
  }
  ngOnChanges() {
    // if (this.inValue) {
    //   this.copy();
    // }
    this.addPartyRelationshipBean = this.inValue;
    let a = _.clone(this.addPartyRelationshipBean);
    if (a.idenExpiraDate && a.idenExpiraDate != '') {
      if (this.commfunc.transDate(new Date(a.idenExpiraDate)) < this.commfunc.transDate(new Date())) {
        this.showDate = 2;
      } else if (this.commfunc.transDate(new Date(a.idenExpiraDate)) == this.commfunc.transDate(new Date())) {
        this.showDate = 1;
      } else {
        this.showDate = 0;
      }
    }
  }
  // copy() {
  //   this.param = _.clone(this.inValue);
  //   this.httpService.selectPartyRelationship({ custNo: this.custNo, relationshipCustNo: this.param.relationshipCustNo, relationshipType: this.param.relationshipType }).subscribe(data => {
  //     if (data.returnCode.code == 'success') {
  //       if (data.partyRelationshipList.length > 0) {
  //         let param1 = data.partyRelationshipList[0];
  //         this.addPartyRelationshipBean = param1;
  //       }
  //     }
  //   })

  // }

  //提交按钮
  submitBtn() {
    console.log(this.addPartyRelationshipBean)
    //校验的
    for (const i in this.distributeCustQryForm.controls) {
      this.distributeCustQryForm.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.distributeCustQryForm.controls) {
      if (!this.distributeCustQryForm.controls[i].valid) {
        return;
      }
    }
    let param = _.clone(this.addPartyRelationshipBean);
    param.birthDate = this.commfunc.transDate(new Date(param.birthDate));
    param.idenExpiraDate = this.commfunc.transDate(new Date(param.idenExpiraDate));
    // this.addPartyRelationshipBean.weakIdenFlag = '1';//强弱关联标识，后台改造完毕后删除
    // this.custNo = this.addPartyRelationshipBean.custNo;
    let ob = this.httpService.updateKeyBaseInfo(param);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.router.navigate(['pages/tzb/workflow/custom-workflow/association-relation',data.dataMap]);
        this.outValue.emit('修改关键人');
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务！' });
    })
  }
  //取消按钮
  modifyCloseBtn() {
    this.outValue.emit('取消');
    // this.addPartyRelationshipBean= new AddPartyRelationshipBean();
  }
  onKeyupNumber(event) {
    if (checkNumber(null, event.target.value)) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: '手机号码输入有误' }];
      event.target.value = '';
      return
    }
  }
  onBlurMobile(event, data) {
    if (data == 1) {
      if (event.target.value != '') {
        if (checkTel(null, event.target.value)) {
          this.msgs = [{ severity: 'error', summary: '提示', detail: '手机号码输入有误' }];
          event.target.value = '';
          return
        }
      }
    } else if (data == 2) {
      if (phoneCheck(null, event.target.value)) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: '联系电话输入有误' }];
        event.target.value = '';
        return
      }
    }
  }
  validDate() {

    this.showDate = 0;
    let a = _.clone(this.addPartyRelationshipBean);
    let ret: any = this.commfunc.transDate(new Date())
    let ret1 = ret * 1 + 1;
    let retSelect: any = this.commfunc.transDate(a.idenExpiraDate);
    if (this.commfunc.transDate(a.idenExpiraDate) < this.commfunc.transDate(new Date())) {
      this.showDate = 2;
    } else if (this.commfunc.transDate(a.idenExpiraDate) == ret) {
      this.showDate = 1;
    }
    else if (retSelect == ret1) {
      this.showDate = 1;
    } else {
      this.showDate = 0;
    }
    console.log(this.showDate)
  }
  reset() {
    this.outValue.emit(false);
  }
  keyCheckChinese(event) {
    if (!checkChinese(null, event.target.value)) {
      // this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入数字或字母字符' }];
      event.target.value = '';
      this.showIden = true;
    } else {
      this.showIden = false;
    }
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
}
