  addCall(event: string) {
    if (event == '新增证件' || event == '修改证件') {
      let successMes = '';
      if (event == '新增证件') {
        successMes = '新增成功!'
      } else if (event == '修改证件') {
        successMes = '修改成功!'
      }
      this.msgs = [{ severity: 'success', summary: '提示', detail: successMes }];
      this.addDisplayCertificate = false;
      this.updateDisplayCertificate = false;
      let param = {
        custNo: this.custNo
      }
      this.httpService.selectCustCertificateInfo(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          for (let item of data.idenInfoList) {
            this.httpService.queryParentZoneInfo({ zoneId: item.registeredAddress }).subscribe(data => {
              if (data.returnCode.code == 'success') {
                let param = '';
                data.zoneList = data.zoneList.reverse();
                for (let item1 of data.zoneList) {
                  param += item1.parentZoneName
                }
                item['zoneName'] = param;
              }
            })
            this.certificateitems = data.idenInfoList;
          }
          this.globalState.notifyDataChanged('event:custom-view-refresh', this.inValue);
        }
      });
      document.body.style.overflow = 'auto';
    } else if (event == '新增地址' || event == '修改地址') {
      let successMes = '';
      if (event == '新增地址') {
        successMes = '新增成功!'
      } else if (event == '修改地址') {
        successMes = '修改成功!'
      }
      this.msgs = [{ severity: 'success', summary: '提示', detail: successMes }];
      this.addDisplayAddress = false;
      this.updateDisplayAddress = false;
      let param = {
        custNo: this.custNo
      }
      this.httpService.selectCustAddressInfo(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          for (let item of data.addressList) {
            this.httpService.queryParentZoneInfo({ zoneId: item.address1 }).subscribe(data => {
              if (data.returnCode.code == 'success') {
                let param = '';
                data.zoneList = data.zoneList.reverse();
                for (let item1 of data.zoneList) {
                  param += item1.parentZoneName
                }
                item['zoneName'] = param;
              }
            })
            this.addressitems = data.addressList;
          }
        }
      });
      document.body.style.overflow = 'auto';
    } else if (event == '新增股东' || event == '修改股东') {
      let successMes = '';
      if (event == '新增股东') {
        successMes = '新增成功!'
      } else if (event == '修改股东') {
        successMes = '修改成功!'
      }
      this.msgs = [{ severity: 'success', summary: '提示', detail: successMes }];
      this.httpService.selectHolderBaseInfo({ custNo: this.custNo }).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.shareholdersitems = data.relationList;
        }
        this.addDisplayShareholders = false;
        this.updateDisplayShareholders = false;
      })
      document.body.style.overflow = 'auto';
    } else if (event == '新增关键人' || event == '修改关键人') {
      let successMes = '';
      if (event == '新增关键人') {
        successMes = '新增成功!'
      } else if (event == '修改关键人') {
        successMes = '修改成功!'
      }
      this.msgs = [{ severity: 'success', summary: '提示', detail: successMes }];
      this.httpService.selectKeyBaseInfo({ custNo: this.custNo }).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.keypersonitems = data.relationList;
        }
        this.addDisplayKeyperson = false;
        this.updateDisplayKeyperson = false;
      });
      document.body.style.overflow = 'auto';
    } else if (event == '新增法定代表人') {
      this.msgs = [{ severity: 'success', summary: '提示', detail: '提交审批成功' }];
      this.httpService.selectLegalBaseInfo({ custNo: this.custNo }).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.legalRelatedBaseBeanl = data;
        }
        // this.addDisplayLegal = false;
      })
      this.addDisplayLegal = false;
      document.body.style.overflow = 'auto';
    } else {
      this.addDisplayCertificate = false;
      this.updateDisplayCertificate = false;
      this.addDisplay = false;
      this.updateDisplay = false;
      this.addDisplayAddress = false;
      this.updateDisplayAddress = false;
      this.addDisplayShareholders = false;
      this.updateDisplayShareholders = false;
      this.addDisplayKeyperson = false;
      this.updateDisplayKeyperson = false;
      this.addDisplayLegal = false;
      document.body.style.overflow = 'auto';
    }
    // this.updateDisplayShareholders = false;
  }

