  // 校验集团编号
  docheck(event) {
    if (event.target.value != '') {
      // 校验客户号
      var reg = /^[0-9]*$/;
      if (!reg.test(event.target.value)) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入数字' }];
        this.comCustBasicInfoBean.blocCustName = undefined;
        event.target.value = '';
      } else {
        if (event.target.value.length == 10) {
          this.queryCust();
        } else {
          this.comCustBasicInfoBean.blocCustName = undefined;
        }
      }
    }
  }

