import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
// import { SelectPartyRelationshipBean } from '../bean/selectPartyRelationship.bean';
import { FamilyBean } from '../../../../custom-view/association-relation/personal-relation/bean/addPartyRelationship.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import *  as _ from 'lodash';
import { CALENDAR_CN, yearRangeBig, yearRangeSmall } from 'app/@uisftech/common/constant/config';
import { checkNumber, checkTel, phoneCheck, checkEnAndNum, checkCard, checkChinese } from 'app/@uisftech/common/service/validator';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
@Component({
    selector: 'app-add-family',
    templateUrl: './add-family.component.html',
    styleUrls: ['./add-family.component.scss']
})
export class AddFamilyComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    @Input('key-label') keyLabel: any;
    @Input('kind-do') kindDo: string = '';
    buttonDisabled:boolean = false;
    public addPartyRelationshipBean: FamilyBean = new FamilyBean();
    public panduan: any[];
    public reductionNumberType: any[];
    // public relationshipType: any[] = RelationshipType;
    // public parentTypeId: any[] = ParentTypeId;
    public distributeCustQryForm: FormGroup;//校验
    checkNumber = checkNumber;//校验数字
    checkTel = checkTel;//校验手机号
    phoneCheck = phoneCheck;//校验联系电话
    public custNo: string;
    public items: any[];
    public relationshipType: any = [];//子级关系类型编号
    public relationship: any = [];
    today: Date = new Date();
    ch = CALENDAR_CN;
    academic = [];
    IdenType = [];
    JobTitle = [];
    gender = [];
    Duty = [];
    code: any;
    //提示信息
    public msgs: Message[] = [];
    public show: boolean = true;
    public showEmpty: boolean = true;
    yearRangeSmall: any = yearRangeSmall;
    yearRangeBig: any = yearRangeBig;
    yearRange: any;
    dealPerShow: boolean = false;
    waitSelUserList: any;
    outVal: any;
    custNoPage: string;//当前页面的客户号
    constructor(
        public httpService: CusViewHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public router: Router,
        public httpServiceOper: CusOperationHttpService,
    ) {
        this.yearRange = `${yearRangeSmall}:${yearRangeBig}`;
        this.codeValues();
        this.panduan = this.code['Panduan'];
        this.reductionNumberType = this.code['ReductionNumberType'];
        this.academic = this.code['ACADEMIC'];
        this.IdenType = this.code['IDEN_TYPE'];
        this.JobTitle = this.code['JobTitle'];
        this.gender = this.code['GENDER'];
        this.Duty = this.code['Duty'];
        this.custNoPage = this.commfunc.getSessionData('custNo');


    }

    ngOnInit() {

        this.distributeCustQryForm = this.fb.group({
            'familyCustName': new FormControl('', Validators.required),//姓名
            // 'custNo': new FormControl('', Validators.required),//姓名
            'familyType': new FormControl('', Validators.required),//关系类型
            'gender': new FormControl(''),//性别
            'birthDay': new FormControl(''),//出生日期
            'idenType': new FormControl(''),//证件类型
            'idenNum': new FormControl(''),//证件号码
            'familyCustNo': new FormControl(''),//客户号
            'academic': new FormControl(''),//最高学历
            'telNum': new FormControl(''),//固话
            'workUnit': new FormControl(''),//详细地址
        });
    }

    ngOnChanges() {
        if (this.inValue) {
            this.relationshipType = [{ label: '请选择', value: '' }, { label: '配偶', value: '010102' }, { label: '父母与子女', value: '010103' }];
            this.addPartyRelationshipBean = new FamilyBean();
            this.addPartyRelationshipBean.custNo = this.inValue.custNo;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //提交按钮
    submitBtn() {
        
        if (this.addPartyRelationshipBean.idenType == '0') {
            if (!checkChinese(null, this.addPartyRelationshipBean.idenNum)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入数字或字符' }];
                this.addPartyRelationshipBean.idenNum = '';
                return;
            }
        }
        //校验的
        for (const i in this.distributeCustQryForm.controls) {
            this.distributeCustQryForm.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.distributeCustQryForm.controls) {
            if (!this.distributeCustQryForm.controls[i].valid) {
                return;
            }
        }
        // if (checkTel(null, this.addPartyRelationshipBean.telNum)) {
        //     this.msgs = [{ severity: 'error', summary: '提示', detail: '联系电话输入有误,请输入11位数字正确的联系电话' }];
        //     this.addPartyRelationshipBean.telNum = '';
        //     return;
        // }
        this.buttonDisabled = true;
        if (this.addPartyRelationshipBean.familyType && this.addPartyRelationshipBean.familyType == '010102') {
            if (this.addPartyRelationshipBean.familyCustNo && this.addPartyRelationshipBean.familyCustNo != '') {
                let param = {
                    custNo: this.custNoPage,
                }
                
                this.httpService.selectPartyRelationshipByType(param).subscribe(data => {
                    this.buttonDisabled = false;
                    if (data.returnCode.code == 'success') {
                        if (data.relationshipCustNo == '' || data.relationshipCustNo == null) {
                            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                            if (s.extParam.specifyNextOpers) {
                                delete s.extParam.specifyNextOpers;
                                let commonHeader = s;
                                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                            }
                            this.waitSelUserList = this.commfunc.query('GLGXXZ', {});
                            this.outVal = this.waitSelUserList.waitSelUserList;
                            let chooseThis = this.waitSelUserList.display;
                            console.log(this.dealPerShow);
                            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                            let a2 = commonHeader.extParam.specifyNextOpers;
                            console.log(a2);
                            if (chooseThis == true && (a2 == undefined || a2 == '' || a2 == null)) {
                                this.dealPerShow = true;
                            } else {
                                this.relationAdd();
                            }
                        } else if (this.addPartyRelationshipBean.familyCustNo == data.relationshipCustNo) {
                            this.relationAdd();
                        } else if (this.addPartyRelationshipBean.familyCustNo != data.relationshipCustNo) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: '该客户已经存在夫妻关联关系,请核实!' });
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.buttonDisabled = false;
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
                });
            } else {
                this.relationAdd();
            }
        } else {
            this.relationAdd();
        }

    }
    //新增家庭
    relationAdd() {
        var berthDate = this.addPartyRelationshipBean.birthDate;
        let ob = this.httpService.addFamilyBaseInfo(this.addPartyRelationshipBean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addPartyRelationshipBean = new FamilyBean();
                // this.router.navigate(['pages/tzb/workflow/custom-workflow/association-relation',data.dataMap]);
                this.outValue.emit('新增家庭成员');
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务！' });
        })
    }

    outDisplayCall(event) {
        console.log(event);
        this.dealPerShow = event;
    }
    outValueCall(event) {
        console.log(event);
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        console.log(commonHeader);
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.relationAdd();
    }
    //取消
    reset() {
        this.outValue.emit('取消');
    }

    onKeyupNumber(event) {
        if (checkNumber(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '当前输入框内只能输入数字' }];
            event.target.value = '';
        }
    }
    //通过客户号查询客户名称
    getName() {
        if (this.addPartyRelationshipBean.familyCustNo && this.addPartyRelationshipBean.familyCustNo != '') {
            let param = {
                custNo: this.addPartyRelationshipBean.familyCustNo
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.addPartyRelationshipBean.familyCustName = data.custName;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
            })
        }
    }
    //
    //客户号  回显用户名
    selectCustBriefInfo(event, a) {
        this.addPartyRelationshipBean.familyCustName = '';
        if (a.value.length == 10) {
            let param = {
                custNo: a.value
            }
            this.httpService.selectCustBriefInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    // this.custGroupListBean.custList[i] = data || [];
                    this.addPartyRelationshipBean.familyCustName = data.custName || '';
                    this.addPartyRelationshipBean.telNum = data.contactWay || '';
                    this.addPartyRelationshipBean.gender = data.gender || '';
                    this.addPartyRelationshipBean.idenType = data.idenType || '';
                    this.addPartyRelationshipBean.idenNum = data.idenNum || '';
                    this.addPartyRelationshipBean.workUnit = data.workUnit || '';
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
            })
        }

    }
    onBlurMobile(event, data) {
        if (data == 1) {
            if (event.target.value != '') {
                if (checkTel(null, event.target.value)) {
                    this.msgs = [{ severity: 'error', summary: '提示', detail: '手机号码输入有误' }];
                    event.target.value = '';
                }
            }
        } else if (data == 2) {
            if (phoneCheck(null, event.target.value)) {
                this.msgs = [{ severity: 'error', summary: '提示', detail: '联系电话输入有误' }];
                event.target.value = '';
            }
        }
    }

    checkCard(event) {
        if (!checkChinese(null, event.target.value)) {
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入数字或字符' }];
            event.target.value = '';

        }
    }



}
