    validDate(){
        
        this.showDate=0;
        let a=_.clone(this.addPartyRelationshipBean);
        let ret:any = this.commfunc.transDate(new Date())
        let ret1 = ret*1 +1;
        let retSelect:any = this.commfunc.transDate(a.idenExpiraDate);
        if(this.commfunc.transDate(a.idenExpiraDate)<this.commfunc.transDate(new Date())){
            this.showDate=2;
        }else if(this.commfunc.transDate(a.idenExpiraDate)==ret){
            this.showDate=1;
        }
        else if( retSelect == ret1){ 
            this.showDate=1;
        }else{
            this.showDate=0;
        }  
        console.log(this.showDate)  
    }
